import {
    Component,
    h,
    Prop,
    Element,
    Host,
    Event,
    EventEmitter,
} from '@stencil/core';
import { cva } from 'class-variance-authority';

const variants = cva(
    'group/checkbox transition-[color,background-color, box-shadow] relative flex w-full cursor-pointer items-start gap-8 rounded-md p-8 outline-0 duration-300 ease-in-out focus:outline-0 focus-visible:ring-2 focus-visible:ring-active-foreground/30',
    {
        variants: {
            disabled: {
                false: 'cursor-pointer',
                true: 'pointer-events-none opacity-70 grayscale-[1]',
            },
            checked: {
                false: 'bg-white hover:bg-surface-1',
                true: 'bg-active-foreground/15',
            },
        },
    },
);

const checkboxVariants = cva(
    'material-icons shadow-inset-xs pointer-events-none z-10 !h-16 !w-16 cursor-pointer rounded-sm border border-solid accent-active-foreground ' +
        'transition-all duration-300 ease-in-out',
    {
        variants: {
            checked: {
                false: 'bg-surface-1 group-focus-visible/checkbox:border-2 group-focus-visible/checkbox:border-active-foreground',
                true: 'bg-active-foreground/50 group-focus-visible/checkbox:border-2 group-focus-visible/checkbox:border-active-foreground',
            },
            disabled: {
                false: 'group-focus-visible/checkbox:ring-2 group-focus-visible/checkbox:ring-active-foreground/30',
                true: null,
            },
        },
    },
);

/**
 * @category Form Controls
 * @description A checkbox component for selecting a single option from a predefined list. Provides validation, labeling, and accessibility features for checkbox collections.
 *
 * @slot - Placed after the title & subtitle elements.
 */
@Component({
    tag: 'atui-checkbox',
    shadow: false,
})
export class AtuiCheckbox {
    @Element() el: HTMLElement;

    /**
     * Title of the checkbox component.
     */
    @Prop()
    label?: string;

    /**
     * Subtitle of the checkbox component.
     */
    @Prop()
    hint_text?: string;

    /**
     * Id of the checkbox.
     */
    @Prop()
    option_id?: string;

    /**
     * State of the checkbox
     */
    @Prop({ mutable: true })
    value?: boolean;

    /**
     * Disables interaction with the checkbox
     */
    @Prop()
    disabled: boolean;

    checkboxEl: HTMLInputElement;

    /**
     * When the checkbox is toggled, this will emit true if the current value is checked, vice versa.
     */
    @Event()
    atuiChange: EventEmitter<boolean>;

    handleChange(value): void {
        this.value = value.target.checked;
        this.atuiChange.emit(this.value);
    }

    render() {
        const classname = variants({
            disabled: this.disabled,
            checked: this.value,
        });

        const checkboxClassname = checkboxVariants({
            checked: this.value,
            disabled: this.disabled,
        });

        return (
            <Host
                role="checkbox"
                aria-checked={this.value}
                aria-labelledby={`${this.option_id}-label`}
                tabindex={0}
                class={classname}
                data-name="checkbox-container"
                onKeyDown={(event) =>
                    (event.key === 'Enter' || event.key === ' ') &&
                    this.checkboxEl.click()
                }
                onClick={() => this.checkboxEl.click()}
            >
                <input
                    type="checkbox"
                    class={checkboxClassname}
                    data-name="checkbox-input"
                    checked={this.value}
                    onChange={(event) => this.handleChange(event)}
                    id={this.option_id}
                    tabindex={-1}
                    ref={(el) => (this.checkboxEl = el)}
                    disabled={this.disabled}
                />

                {(this.label || this.hint_text) && (
                    <div class="pointer-events-none flex flex-col">
                        <slot name="label" />
                        {this.label && (
                            <label
                                class="mt-0 pl-4 text-xs font-medium"
                                id={`${this.option_id}-label`}
                                data-name="checkbox-label"
                            >
                                {this.label}
                            </label>
                        )}
                        {this.hint_text && (
                            <span
                                class="mt-0 pl-4 text-xs text-light"
                                data-name="checkbox-hint"
                            >
                                {this.hint_text}
                            </span>
                        )}
                    </div>
                )}
                <slot />
            </Host>
        );
    }
}
